.mh-education-box {
    background-color: #f8f9fa;
    border-radius: 10px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
    padding: 20px;
    
    transition: transform 0.3s, box-shadow 0.3s , background-color 0.3s ease;
    border: 2px solid black;

    margin-top: 15px;
}

.mh-experience{
    border: 2px solid red;
    background-color: #57a6b97c;
    width: 100%;
    height: 100%;
    min-height: 89vh;
    
}

.mh-education-box:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    background-color: #f0f8ff;
}

.mh-education-box{
    margin-top: 20%;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2), 0 6px 20px rgba(0, 0, 0, 0.19);
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
    background-image: linear-gradient(to bottom right, #f7f7f7, #eaeaea);
}

.mh-education-box:hover {
    transform: translateY(-10px) scale(1.02); /* Slightly scale the box and move it up */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 12px 24px rgba(0, 0, 0, 0.2);
    background-color: #f0f8ff; /* Changes to a light blue color on hover */
}

.mh-education h3 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #343a40;
    font-family: 'Lucida Sans', sans-serif;
    font-weight: bold;
}

.mh-education-item h4 {
    font-size: 1.5em;
    margin-bottom: 10px;
    color: #007bff;
    font-weight: bold;
}

.mh-eduyear {
    font-size: 1.2em;
    color: #6c757d;
    margin-bottom: 10px;
    font-weight: bold;
}

.mh-education-item p {
    font-size: 1.1em;
    color: #495057;
    line-height: 1.6;
    font-weight: bold;
}

.mh-education-item a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
}

.mh-education-item a:hover {
    text-decoration: underline;
}


/* Education heading style */
.education {
    text-align: center;
    text-decoration: underline;
    color: rgb(239, 253, 112); /* Final color */
    background: linear-gradient(90deg, transparent, transparent); /* Gradient background to mask the color transition */
    background-clip: text; /* Clip background to text */
    -webkit-background-clip: text; /* For Safari */
    font-size: 40px;
    font-weight: bold;
    opacity: 0; /* Start as invisible */
    animation: fadeInColor 1s forwards; /* Apply the animation */
}

/* Keyframes for opacity and color transition */
@keyframes fadeInColor {
    from {
        opacity: 0; /* Start as invisible */
        color: white; /* Start with white color */
    }
    to {
        opacity: 1; /* End as fully visible */
        color: rgb(248, 103, 116); /* End with your desired color */
    }
}

